import "nprogress/nprogress.css"
import NotFound from "../views/404.vue"
//导入进度条插件的css文件
import { start, done, configure } from "nprogress"
//只需要start、done、configure三个函数，所以这里使用具名导入

configure({
    //进度条设置
    trickleSpeed: .4,
    //速度
    showSpinner: false
    //进度条右上角是否显示小圆圈
})

/**
 * 模拟网络延迟函数
 * @param {*} duations 延迟多长时间
 */
function delay(duations) {
    return new Promise(resolve => {
        setTimeout(() => {
            resolve();
        }, duations);
    })
}


/**
 * 调用该函数，实现异步组件，并实现进度条
 * @param {*} routerComponent 调用该函数，返回匹配路由路径
 * @returns 
 */
function gerRouterComponent(routerComponent) {
    return async () => {
        //返回一个异步函数
        start()
        //进度条开始
        if (process.env.NODE_ENV === "development") {
            await delay(2000);
        }
        const resp = await routerComponent();
        //拿到路由路径
        done();
        //进度条结束
        return resp;
        //返回路由路径
    }
}


export default [
    {
        name: "Home", path: "/", meta: { title: "首页", }, component: gerRouterComponent(() => {
            return import("@/views/Home")
        })
    },
    {
        name: "About", path: "/about", meta: { title: "关于我" }, component: gerRouterComponent(() => {
            return import("@/views/About")
        })
    },
    {
        name: "Blog", path: "/blog", meta: { title: "文章列表" }, component: gerRouterComponent(() => {
            return import("@/views/Blog")
        })
    },
    {
        name: "CategoryBlog", path: "/blog/cate/:categoryId", meta: { title: "文章列表" }, component: gerRouterComponent(() => {
            return import("@/views/Blog")
        })
    },
    //可以匹配/blog/cate/后面的路径，是动态的，地址脸/blog/cate后面跟啥。就是啥!
    {
        name: "BlogDetail", path: "/blog/:id", meta: { title: "文章详情" }, component: gerRouterComponent(() => {
            return import("@/views/Blog/Detail")
        })
    },
    //匹配该组件，在地址栏中，  localhost://blog/BlogDetail/xxxxx，
    //这里的xxxxx就是:id(动态的)，
    //我们拿到动态id之后，可以通过该id获取对应的评论、详情页信息!
    {
        name: "Message", path: "/message", meta: { title: "留言板" }, component: gerRouterComponent(() => {
            return import("@/views/Message")
        })
    },
    {
        name: "Project", path: "/project", meta: { title: "项目&效果" }, component: gerRouterComponent(() => {
            return import("@/views/Project")
        })
    },
    {
        name: "NotFound",
        path: "*",
        component: NotFound
    }
]