let routerTitle = '', storeTitle = '';
//设置两个变量保存一下当前的title
function setTitle() {
    /**
     * 通过该函数设置title
     */
    if (!routerTitle && !storeTitle) {
        //如果两个变量都没有值，则设置loading...
        document.title = "loading..."
    } else if (!routerTitle && storeTitle) {
        document.title = storeTitle;
    } else if (routerTitle && !storeTitle) {
        document.title = routerTitle;
        //如果其中一个有值，则设置有值的变量
    } else {
        document.title = `${routerTitle}-${storeTitle}`;
        //如果两个都有值，则一起设置
    }
}

export default {
    //导出这两个方法
    setRouterTitle(title) {
        //该方法是设置路由title的，当页面已经跳转过去之后触发(router.afterEach)
        routerTitle = title
        //然后将上面声明的routerTitle变量设值
        setTitle()
        //最后调用通过该函数设置title方法
    },
    setStoreTitle(title) {
        //该方法是设置仓库title的，当全局设置数据获取成功过后触发
        storeTitle = title
        setTitle()
    }
}