<template>
  <div
    class="top-container"
    v-show="show"
    ref="topContainer"
    @click="hanlderTop"
  >
    top
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
    };
  },
  created() {
    this.$bus.$on("mainScroll", this.hanlderScroll);
  },
  destroyed() {
    this.$bus.$off("mainScroll", this.hanlderScroll);
  },
  methods: {
    hanlderScroll(dom) {
      if (!dom) {
        this.show = false;
        return;
      }
      this.show = dom.scrollTop >= 500;
    },
    hanlderTop() {
      this.$bus.$emit("mainScrollTop", 0);
    },
  },
};
</script>

<style lang="less" scoped>
.top-container {
  position: fixed;
  right: 50px;
  bottom: 50px;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  line-height: 50px;
  cursor: pointer;
  text-align: center;
  color: #fff;
  transition: 0.5s;
  background: rgb(35, 140, 193);
  &:hover {
    transform: scale(1.3);
    background: rgb(4, 155, 231);
  }
}
</style>
