import eventBus from "../../eventBus"
import { shake } from "@/utils"
import defaultGIF from "@/assets/default.gif"

//希望通过该数组来确定哪些图片是需要加载的，哪些是不需要加载的，
let imgs = []


/**
 * 希望，调用该函数，就可以设置那些合适的图片!
 */
function setImages() {
    for (const img of imgs) {
        setImage(img);
    }
}
function setImage(img) {
    img.dom.src = defaultGIF
    //img.dom.src是真实的路径
    const rect = img.dom.getBoundingClientRect();
    //拿到当前img的坐标
    const clientHeight = document.documentElement.clientHeight;
    //拿到可见视口的高度
    const height = rect.height || 100
    //拿到img的高度，如果没有值或者为0， 那么默认用150
    if (rect.top >= -height && rect.top <= clientHeight) {
        //如果当前img的top值 大于等于了 -img的高度 
        //并且img的top值 小于等于的 可见视口高度
        //说明当前是在可见视口范围内，需要加载
        const tempImage = new Image();
        //创建一个img， 
        tempImage.onload = function () {
            //img默认都有一个onload事件（图片加载完成之后触发）
            img.dom.src = img.src
            //把真实的img路径赋值成真实图片路径
        }
        tempImage.src = img.src;
        //保存一下真实的图片路径
        imgs = imgs.filter(i => i !== img)
        //把已经加载完成的img图片删掉

    }
}


function hanlderScroll() {
    // console.log("滚动了img")
    setImages();
}
eventBus.$on("mainScroll", shake(hanlderScroll, 1000))


/**
 * 导出的是一个配置对象，配置自定义指令的
 */
export default {
    inserted(el, bindings) {
        //bind钩子函数，该函数在dom被创建的事件触发
        //el：所绑定自定义指令的元素
        //bindins：所绑定自定义指令元素的值，其中包括value
        const img = {
            //往imgs数组中添加绑定了自定义指令的img图片
            dom: el,
            //img元素
            src: bindings.value,
            //真实的img图片地址
        }
        imgs.push(img);
        setImage(img);
    },
    unbind(el) {
        imgs = imgs.filter((img) => img.dom !== el)
        //当元素被销毁时触发（也就是切换组件时），
        //通过过滤实现， 将imgs重新赋值， 
        //先把imgs中所有的dom拿出来一个一个看，  如果imgs中的dom不等于当前的el，
        //说明imgs里的新值是需要被加载的!
    }
}


