export default function (timestamp, showTime = false) {
    const data = new Date(+timestamp);
    const month = ((data.getMonth() + 1).toString().padStart(2, "0"));
    const day = data.getDate().toString().padStart(2, "0");
    const hours = data.getHours().toString().padStart(2, "0");
    const minute = data.getMinutes().toString().padStart(2, "0");
    const second = data.getSeconds().toString().padStart(2, "0");
    if (showTime) {
        return `${data.getFullYear()}-${month}-${day}  ${hours}:${minute}:${second}`
    } else {
        return `${data.getFullYear()}-${month}-${day}`
    }
}