import Vue from "vue"
import Vuex from "vuex"
import banner from "./banner"
import setting from "./setting"
import about from "./about"
import project from "./project"
import message from "./message"
Vue.use(Vuex);

const store = new Vuex.Store({
    modules: {
        message,
        banner,
        project,
        about,
        setting
    }
})

window.store = store;
export default store;