import Vue from "vue";
import VueRouter from "vue-router";
import routers from "./routers"
import { setTitle } from "../utils"
Vue.use(VueRouter);
const router = new VueRouter({
    routes: routers,
    mode: "history",
});
router.afterEach((to, from) => {
    if (to.meta.title) {
        setTitle.setRouterTitle(to.meta.title)
    }
})
export default router;