import request from "./request";
/**
 * 获取博客数据
 * @param {*} page 当前页码
 * @param {*} limit 页容量
 * @param {*} categoryId 是否全部显示，-1 === 全部显示
 */
export async function getBlogs(page = 1, limit = 10, categoryId = -1) {
    return await request.get("/api/blog", {
        params: {
            page,
            limit,
            categoryId
        }
    });
}

/**
 * 获取博客分类
 */
export async function getBlogTypes() {
    return await request.get("./api/blogtype");
}


/**
 * 获取单个博客信息
 * @param {*} id id为博客的id
 */
export async function getBlog(id) {
    return await request.get(`/api/blog/${id}`)
}


/**
 * 提交评论，comment === 需要提交的评论
 * @param {*} Comment 
 * @returns 
 */
export async function postComment(Comment) {
    return await request.post("/api/comment", Comment);
}

/**
 * 分页获取评论
 * @param {*} blogid 
 * @param {*} page 
 * @param {*} limit 
 * @returns 
 */

export async function getComments(blogid, page = 1, limit = 10) {
    return await request.get("/api/comment", {
        params: {
            //这里的params代表的是   http://www.baidu.com?xxxx&xxxx （？后面的数据）
            blogid,
            page,
            limit
        }
    })
}

/**
 * 获取关于我页面数据
 */
export async function about() {
    return await request.get("/api/about");
}

export async function getMessage(blogid, page = 1, limit = 10) {
    return await request.get("/api/message", {
        params: {
            //这里的params代表的是   http://www.baidu.com?xxxx&xxxx （？后面的数据）
            blogid,
            page,
            limit
        }
    })
}