<template>
  <div class="Menu-container">
    <RouterLink
      :exact="item.exact"
      :to="{ name: item.name }"
      v-for="item in items"
      :key="item.link"
      active-class="selected"
      exact-active-class=""
    >
      <div class="Icon"><Icon :type="item.icon" /></div>
      <div class="sp">
        <span>{{ item.title }}</span>
      </div>
    </RouterLink>
  </div>
</template>

<script>
import Icon from "../Icon/index.vue";
export default {
  components: {
    Icon,
  },
  data() {
    return {
      items: [
        {
          name: "Home",
          title: "首页",
          icon: "home",
          exact: true,
        },
        {
          name: "Blog",
          title: "文章",
          icon: "blog",
          exact: false,
        },
        {
          name: "About",
          title: "关于我",
          icon: "about",
          exact: true,
        },
        {
          name: "Project",
          title: "项目&效果",
          icon: "code",
          exact: true,
        },
        {
          name: "Message",
          title: "留言板",
          icon: "chat",
          exact: true,
        },
      ],
    };
  },
  methods: {
    isSelect(item) {
      var link = item.link.toLowerCase();
      //拿到item数组中link的值，并且转换成小写！
      var curPathNmae = location.pathname.toLowerCase();
      //获取到当前url地址，并且转换成小写！
      if (item.starsWith) {
        //判断item中是否有starsWith值，如果有，直接返回当前url地址中是否是以(有starsWith值的这个数组中的link开头)
        return curPathNmae.startsWith(link);
      } else {
        return curPathNmae === link;
        //否则就判断当前url地址是否和item中每位link相等!

        //1、这里注意，传进来的item是遍历过后的，里面的值是{},{},{},{}
        //第一次判断，假设当前的url地址是/，执行:class要往里面加类名的时候，执行isSelect函数，此时拿到第一个item里的link，然后拿到当前url地址，
        //第一个link值是：/，当前值也是/，但是第一个item里面没有starsWith值，所以判断不成立，则执行else;

        //2、假设此时点击里第二个a标签，页面重新刷新，isSelect函数重新执行，此时link值是:/blog，当前url地址也是/blog，
        //但是第二个item中有starsWith值，所以进入判断，（判断的是，当前地址是否是以第二个item中的link开头，如果是则返回true；）
        //这里进行判断是为了防止后面文章列表会出现各种路径如：/blog/item/xxxx/xxxx/xxx，
      }
    },
  },
};
</script>

